<script>
  import { onMount } from 'svelte';
  import { registerComponentByName, loadComponentByName, preloadComponents } from '$lib/componentLoader.js';

  // Đăng ký component động
  registerComponentByName('UserDetail', () => import('@components/tabs/details/UserDetail.svelte'));
  registerComponentByName('ResultDetail', () => import('@components/tabs/details/ResultDetail.svelte'));

  let DynamicComponent = null;

  async function openInvoice() {
    DynamicComponent = await loadComponentByName('InvoiceDetail');
  }

  onMount(async () => {
    // preload trước để lần sau mở nhanh hơn
    await preloadComponents(['UserDetail', 'ResultDetail']);
  });
</script>

<button on:click={openInvoice}>Mở Invoice</button>

{#if DynamicComponent}
  <svelte:component this={DynamicComponent} someProp="123" />
{/if}
